import { useState } from 'react'

const EmploymentPre = () => {
  const [milVisible, setMilVisible] = useState("none")
  const [disVisible, setDisVisible] = useState("none")
  const [milShowDropVis, setMilShowDropVis] = useState("none")
  const [disShowDropVis, setDisShowDropVis] = useState("none")
  const [milValue, setMilValue] = useState("")
  const [disValue, setDisValue] = useState("1")
  const [milFontColor, setMilFontColor] = useState("gray")
  const [disFontColor, setDisFontColor] = useState("gray")

  const MilitaryBox = () => {
    return (
      <ul>
        <li><button onMouseDown={(e)=>{SelectValue(e, '')}} onBlur={() => {HideDrop('')}}></button></li>
        <li><button onMouseDown={(e)=>{SelectValue(e, '')}} onBlur={() => {HideDrop('')}}></button></li>
        <li><button onMouseDown={(e)=>{SelectValue(e, '')}} onBlur={() => {HideDrop('')}}></button></li>
        <li><button onMouseDown={(e)=>{SelectValue(e, '')}} onBlur={() => {HideDrop('')}}></button></li>
      </ul>
    )
  }

  const DisabilityBox = () => {
    return (
      <ul>
        <li><button onMouseDown={(e)=>{SelectValue(e, '')}} onBlur={() => {HideDrop('')}}></button></li>
        <li><button onMouseDown={(e)=>{SelectValue(e, '')}} onBlur={() => {HideDrop('')}}></button></li>
        <li><button onMouseDown={(e)=>{SelectValue(e, '')}} onBlur={() => {HideDrop('')}}>1</button></li>
        <li><button onMouseDown={(e)=>{SelectValue(e, '')}} onBlur={() => {HideDrop('')}}>2</button></li>
        <li><button onMouseDown={(e)=>{SelectValue(e, '')}} onBlur={() => {HideDrop('')}}>3</button></li>
        <li><button onMouseDown={(e)=>{SelectValue(e, '')}} onBlur={() => {HideDrop('')}}>4</button></li>
        <li><button onMouseDown={(e)=>{SelectValue(e, '')}} onBlur={() => {HideDrop('')}}>5</button></li>
        <li><button onMouseDown={(e)=>{SelectValue(e, '')}} onBlur={() => {HideDrop('')}}>6</button></li>
      </ul>
    )
  }


  const showMilRow = (e) => {
    if (e.target.checked)
      setMilVisible("inline-block")
    else
      setMilVisible("none")
  }

  const showDisRow = (e) => {
    if (e.target.checked)
    setDisVisible("inline-block")
    else
    setDisVisible("none")
  }

  const milShowDrop = () => {
    setMilShowDropVis("block")
  }

  const disShowDrop = () => {
    setDisShowDropVis("block")
  }

  const HideDrop = (item) => {
    item === '' ? setDisShowDropVis('none') : setMilShowDropVis('none')
  }

  const SelectValue = (e, item) => {
    console.log(e)
    item === '' ? setDisFontColor("black") : setMilFontColor("black")
    item === '' ? setDisValue(e.target.innerText) : setMilValue(e.target.innerText)
    HideDrop(item)
  }


  return (
    <div className="basicInfo " id="" style={{zIndex: 40}}>

      <h2></h2>

      <div style={{ margin: "20px 0px 20px 0px" }}>
        <div className='checkBox'>
          <input type="checkbox"></input>
          <span></span>
        </div>
        <div className='checkBox'>
          <input type="checkbox"></input>
          <span> </span>
        </div>
        <div className='checkBox'>
          <input type="checkbox"></input>
          <span> </span>
        </div>
        <div className='checkBox'>
          <input onChange={showDisRow} type="checkbox"></input>
          <span></span>
        </div>
        <div className='checkBox'>
          <input onChange={showMilRow} type="checkbox"></input>
          <span></span>
        </div>
      </div>

      <div className='row' style={{ display: disVisible, zIndex: 100,  }}>
        <div className="inputBox " id="inputBox " >
          <button onClick={disShowDrop} onBlur={() => {HideDrop('')}}>
            <span></span>
            <span style={{ fontSize: "20px", color: disFontColor, height: "45px" }}>{disValue}</span>
          </button>
          <div className='dropBox' style={{ display: disShowDropVis }}><DisabilityBox /></div>
        </div>
      </div>

      <div className='row' style={{ display: milVisible, zIndex: 90 }}>
        <div className="inputBox " id="inputBox " >
          <button onClick={milShowDrop} onBlur={() => {HideDrop('')}}>
            <span></span>
            <span style={{ fontSize: "20px", color: milFontColor, height: "45px" }}>{milValue}</span>
          </button>
          <div className='dropBox' style={{ display: milShowDropVis }}><MilitaryBox /></div>
        </div>
      </div>

    </div>
  )
}

export default EmploymentPre