
const ResumeCompleted = () => {
  const opnePreview = () => {
    window.open("preview", "preview", "width=800, height=900")
  }

  const saveResume = () => {
    alert("!")
  }

  return (
    <div className='ResumeCompleted'>
      <button onClick={opnePreview}></button>
      <button onClick={saveResume}> </button>
    </div>
  )
}

export default ResumeCompleted;