import React, { useState, useRef } from 'react';
import { inputBoxFocus, inputBoxBlur } from './Event/InputEvent'

const LanguageStudy = () => {
  const [addLan, setAddLan] = useState([])
  const [lanEvent, setLanEvent] = useState(false)
  const nextId = useRef(0);

  const addLanEvent = () => {
    const lan = {
      id: "lan" + nextId.current
    };

    setAddLan(addLan.concat(lan));
    setLanEvent(true)
    nextId.current += 1;
    console.log(lanEvent)
  }

  const deleteLan = (e) => {
    setAddLan(addLan.filter((item) => (item.id !== e.target.id)))
    setLanEvent(true)
  }

  const AddLanCreate = () => {
    if (lanEvent) return (
      addLan.map((item) => (
        <div className="addDiv" key={item.id}>
          <div className="row">
            <div className="inputBox " id={"inputBox " + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={"" + item.id} type="text" placeholder=""></input>
            </div>
            <div className="inputBox " id={"inputBox " + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={"" + item.id} type="text" placeholder="TOEIC"></input>
            </div>
            <div className="inputBox -" id={"inputBox -" + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={"-" + item.id} type="text" placeholder="2006.02"></input>
            </div>
            <button onClick={deleteLan} id={item.id}></button>
          </div>
        </div>
      ))
    )
  }

  return (
    <div className="basicInfo " id="" style={{zIndex: 60}}>

      <h2>
        <button onClick={addLanEvent} className='addButton'>+</button>
      </h2>

      <div className="row">
        <div className="inputBox " id="inputBox ">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder=""></input>
        </div>
        <div className="inputBox " id="inputBox ">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder="TOEIC"></input>
        </div>
        <div className="inputBox -" id="inputBox -">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="-" type="text" placeholder="2006.02"></input>
        </div>
      </div>

      <AddLanCreate/>

    </div>
  )
}

export default LanguageStudy