import { inputBoxFocus, inputBoxBlur } from './Event/InputEvent'

const Introduction = () => {
  return (
    <div className="basicInfo " id="" style={{zIndex: 30}}>

      <h2></h2>

      <div className="row">
        <div className="inputBox " id="inputBox ">
          <span></span>
          <textarea onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder=""></textarea>
        </div>
      </div>

    </div>
  )
}

export default Introduction