import React, { useState, useRef } from 'react';
import { inputBoxFocus, inputBoxBlur } from './Event/InputEvent'

const Internship = () => {
  const [addInter, setaddInter] = useState([])
  const [interEvent, setInterEvent] = useState(false)
  const nextId = useRef(0);

  const addInterEvent = () => {
    const inter = {
      id: "inter" + nextId.current
    };

    setaddInter(addInter.concat(inter));
    setInterEvent(true)
    nextId.current += 1;
    console.log(interEvent)
  }

  const deleteInter = (e) => {
    setaddInter(addInter.filter((item) => (item.id !== e.target.id)))
    setInterEvent(true)
  }

  const AddInterCreate = () => {
    if (interEvent) return (
      addInter.map((item) => (
        <div className="addDiv" key={item.id}>
          <div className="row">
            <div className="inputBox " id={"inputBox " + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={"" + item.id} type="text" placeholder=""></input>
            </div>
            <div className="inputBox " id={"inputBox " + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={"" + item.id} type="text" placeholder=""></input>
            </div>
            <div className="inputBox " id={"inputBox " + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={"" + item.id} type="text" placeholder="2006.02"></input>
            </div>
            <div className="inputBox " id={"inputBox " + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={"" + item.id} type="text" placeholder="2006.02"></input>
            </div>
            <button onClick={deleteInter} id={item.id}></button>
          </div>

          <div className="row">
            <div className="inputBox " id={"inputBox " + item.id}>
              <span></span>
              <textarea onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={"" + item.id} type="text" placeholder=""></textarea>
            </div>
          </div>
        </div>
      ))
    )
  }


  return (
    <div className="basicInfo " id="" style={{zIndex: 50}}>

      <h2>
        <button onClick={addInterEvent} className='addButton'>+</button>
      </h2>

      <div className="row">
        <div className="inputBox " id="inputBox ">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder=""></input>
        </div>
        <div className="inputBox " id="inputBox ">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder=""></input>
        </div>
        <div className="inputBox " id="inputBox ">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder="2006.02"></input>
        </div>
        <div className="inputBox " id="inputBox ">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder="2006.02"></input>
        </div>
      </div>

      <div className="row">
        <div className="inputBox " id="inputBox ">
          <span></span>
          <textarea onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder=""></textarea>
        </div>
      </div>

      <AddInterCreate/>

    </div>
  )
}

export default Internship