import React, { useState, useRef } from 'react';
import { inputBoxFocus, inputBoxBlur } from './Event/InputEvent'

const Career = () => {
  const [addCar, setAddCar] = useState([])
  const [carEvent, setCarEvent] = useState(false)
  const nextId = useRef(0);

  const addCarEvent = () => {
    const car = {
      id: "car" + nextId.current
    };

    setAddCar(addCar.concat(car));
    setCarEvent(true)
    nextId.current += 1;
    console.log(carEvent)
  }

  const deleteCar = (e) => {
    setAddCar(addCar.filter((item) => (item.id !== e.target.id)))
    setCarEvent(true)
  }

  const inService = (e) => {
    const element = document.getElementById("inputBox ")
    if(e.target.checked) {
      element.style.display = "none"
    } else {
      element.style.display = "inline-block"
    }
    console.log(element)
  }

  const addInService = (e) => {
    const element = document.getElementById("inputBox " + e.target.id)
    if(e.target.checked) {
      element.style.display = "none"
    } else {
      element.style.display = "inline-block"
    }
  }

  const AddCarCreate = () => {
    if (carEvent) return (
      addCar.map((item) => (
        <div className="addDiv" key={item.id}>
          <div className="row">
            <div className="inputBox " id={"inputBox " + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={"" + item.id} type="text" placeholder="OO"></input>
            </div>
            <div className="inputBox " id={"inputBox " + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={"" + item.id} type="text" placeholder="2002.03"></input>
            </div>
            <div className="inputBox " id={"inputBox " + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={""  + item.id} type="text" placeholder="2006.02"></input>
            </div>
            <div className='checkBox' style={{ margin: "0" }}>
              <input onChange={addInService} type="checkbox" id={item.id}></input>
              <span></span>
              <button onClick={deleteCar} id={item.id}></button>
            </div>
          </div>

          <div className="row">
            <div className="inputBox " id={"inputBox " + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={"" + item.id} type="text" placeholder=""></input>
            </div>
            <div className="inputBox " id={"inputBox " + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={"" + item.id} type="text" placeholder=" "></input>
            </div>
            <div className="inputBox " id={"inputBox " + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={"" + item.id} type="text" placeholder="4000"></input>
            </div>
          </div>
        </div>
      ))
    )
  }

  return (
    <div className="basicInfo " id="" style={{zIndex: 80}}>

      <h2>
        <button onClick={addCarEvent} className='addButton'>+</button>
      </h2>

      <div className="row">
        <div className="inputBox " id="inputBox ">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder="OO"></input>
        </div>
        <div className="inputBox " id="inputBox ">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder="2002.03"></input>
        </div>
        <div className="inputBox " id="inputBox ">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder="2006.02"></input>
        </div>
        <div className='checkBox' style={{ margin: "0" }}>
          <input onChange={inService} type="checkbox"></input>
          <span></span>
        </div>
      </div>

      <div className="row">
        <div className="inputBox " id="inputBox ">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder=""></input>
        </div>
        <div className="inputBox " id="inputBox ">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder=" "></input>
        </div>
        <div className="inputBox " id="inputBox ">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder="4000"></input>
        </div>
      </div>

      <AddCarCreate/>

    </div>
  )
}

export default Career