import React, { useEffect, useState, useRef } from 'react';
import { inputBoxFocus, inputBoxBlur } from './Event/InputEvent';

const Edu1 = () => {
  return (
    <div>
      <div className="row">
        <div className="inputBox " id="inputBox ">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder="OO"></input>
        </div>
        <div className="inputBox " id="inputBox ">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder="2002.03"></input>
        </div>
        <div className="inputBox " id="inputBox ">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder="2006.02"></input>
        </div>
      </div>
    </div>
  )
}

const Edu2 = () => {
  const [noGed, setNoGed] = useState("inline-block")
  const [yesGed, setYesGed] = useState("none")
  const [, ] = useState("")

  const GedChk = (e) => {
    const elemet = document.getElementById("")
    if (e.target.checked) {
      setNoGed("none")
      setYesGed("inline-block")
      ("")
      elemet.value = ""
    } else {
      setNoGed("inline-block")
      setYesGed("none")
      ("")
      elemet.value = ""
    }
  }

  return (
    <div>
      <div className="row">
        <div className="inputBox " id="inputBox ">
          <span>{}</span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder="OO"></input>
        </div>
        <div className="inputBox " id="inputBox " style={{ display: noGed }}>
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder="2002.03"></input>
        </div>
        <div className="inputBox " id="inputBox " style={{ display: noGed }}>
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder="2006.02"></input>
        </div>
        <div className="inputBox " id="inputBox " style={{ display: yesGed }}>
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder="2006.02"></input>
        </div>
        <div className='checkBox' style={{ margin: "0" }}>
          <input onChange={GedChk} type="checkbox" id="GED"></input>
          <span></span>
        </div>
      </div>
    </div>
  )
}

