const Education = () => {
  const [addEdu, setAddEdu] = useState([])
  const [eduEvent, setEduEvent] = useState(false)
  const nextId = useRef(0);

  const addEduEvent = () => {
    const edu = {
      id: "edu" + nextId.current
    };

    setAddEdu(addEdu.concat(edu));
    setEduEvent(true)
    nextId.current += 1;
    console.log(eduEvent)
  }

  const deleteEdu = (e) => {
    setAddEdu(addEdu.filter((item) => (item.id !== e.target.id)))
    setEduEvent(true)
  }

  const AddEduCreate = () => {
    if (eduEvent) return (
      addEdu.map((item) => (
        <div className="addDiv" key={item.id}>
          <div className="row">
            <div className="inputBox " id={"inputBox " + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={"" + item.id} type="text" placeholder="OO"></input>
            </div>
            <div className="inputBox " id={"inputBox " + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={"" + item.id} type="text" placeholder="2002.03"></input>
            </div>
            <div className="inputBox " id={"inputBox " + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={"" + item.id} type="text" placeholder="2006.02"></input>
            </div>
            <button onClick={deleteEdu} id={item.id}></button>
          </div>
        </div>
      ))
    )
  }

  const EducheckBox = [
    { id: "EducheckBox1", name: " " },
    { id: "EducheckBox2", name: "" },
    { id: "EducheckBox3", name: "(2,3)" },
    { id: "EducheckBox4", name: "(4)" },
  ]

  const [preCheck, setPreCheck] = useState("")

  const checkBoxChange = (e) => {
    console.log("pre: " + preCheck, "curr: " + e.target.id, e.target.checked)

    if (e.target.id === preCheck) {
      e.target.checked = true
      console.log(e.target.checked)
    } else {
      const disableBox = EducheckBox.filter((item) => (item.id !== e.target.id))
      disableBox.map((item) => (document.getElementById(item.id).checked = false))
      setPreCheck(e.target.id)
    }
  }

  const EducheckBoxList = EducheckBox.map((item) => (
    <div key={item.id} className='checkBox'>
      <input onChange={checkBoxChange} type="checkbox" id={item.id}></input>
      <span>{item.name}</span>
    </div>
  ))

  const SelectEdu = () => {
    switch (preCheck) {
      case 'EducheckBox1': return <Edu1 />
      case 'EducheckBox2': return <Edu2 />
      case 'EducheckBox3': return <Edu3 />
      case 'EducheckBox4': return <Edu4 />
      default: return <Edu1 />
    }
  }

  useEffect(() => {
    const element = document.getElementById("EducheckBox1")
    element.checked = true
    setPreCheck(element.id)
  }, [])

  return (
    <div className="basicInfo " id="" style={{zIndex: 90}}>

      <h2>
        <button onClick={addEduEvent} className='addButton'>+</button>
        <div className='checkBoxWrap'>
          {EducheckBoxList}
        </div>
      </h2>

      <SelectEdu/>
      <AddEduCreate/>

    </div>
  )

}

export default Education