import React, { useState, useRef } from 'react';
import { inputBoxFocus, inputBoxBlur } from './Event/InputEvent'

const Certificate = () => {
  const [addCer, setAddCer] = useState([])
  const [cerEvent, setCerEvent] = useState(false)
  const nextId = useRef(0);

  const addCerEvent = () => {
    const cer = {
      id: "cer" + nextId.current
    };

    setAddCer(addCer.concat(cer));
    setCerEvent(true)
    nextId.current += 1;
    console.log(cerEvent)
  }

  const deleteCer = (e) => {
    setAddCer(addCer.filter((item) => (item.id !== e.target.id)))
    setCerEvent(true)
  }

  const AddCerCreate = () => {
    if (cerEvent) return (
      addCer.map((item) => (
        <div className="addDiv" key={item.id}>
          <div className="row">
            <div className="inputBox " id={"inputBox " + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={"" + item.id} type="text" placeholder=""></input>
            </div>
            <div className="inputBox " id={"inputBox " + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={""  + item.id} type="text" placeholder=""></input>
            </div>
            <div className="inputBox -" id={"inputBox -" + item.id}>
              <span></span>
              <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id={"-"  + item.id} type="text" placeholder="2006.02"></input>
            </div>
            <button onClick={deleteCer} id={item.id}></button>
          </div>
        </div>
      ))
    )
  }

  return (
    <div className="basicInfo " id="" style={{zIndex: 70}}>

      <h2>
        <button onClick={addCerEvent} className='addButton'>+</button>
      </h2>

      <div className="row">
        <div className="inputBox " id="inputBox ">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder=""></input>
        </div>
        <div className="inputBox " id="inputBox ">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="" type="text" placeholder=""></input>
        </div>
        <div className="inputBox -" id="inputBox -">
          <span></span>
          <input onFocus={inputBoxFocus} onBlur={inputBoxBlur} id="-" type="text" placeholder="2006.02"></input>
        </div>
      </div>

      <AddCerCreate/>

    </div>
  )
}

export default Certificate